#register-form {
  display: flex;
  flex-direction: column;
}
label {
  font-size: 1.2rem;
}
input {
  padding: 14px 20px;
  margin: 0;
  font-size: 1.2rem;
  border-radius: 20px;
}

input[type="submit"] {
  margin-top: 10px;
  background: #ffd0c4;
  cursor: pointer;
}

.ok {
  color: green;
}
